.activity {
  width: 100%;
}

.activity ul li {
  width: 100%;
  padding: 1rem;
  font-size: 1.3rem;
  -webkit-transition: background 0.45s ease-in;
  -o-transition: background 0.45s ease-in;
  transition: background 0.45s ease-in;
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.activity ul li img {
  width: 3rem;
  height: 3rem;
}

.activity ul li:hover {
  background: rgb(243, 243, 243);
}


