.overlay {
  width: 100%;
  z-index: 10000;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(8, 8, 8, 0.682);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.post-details {
  background: #fff;
  margin: auto auto;
}

.post-details .header {
  width: 50%;
  padding: 0.5rem;
}

.post-details .header a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.post-details .header img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.post-details .header p {
  display: inline-block;
  color: rgba(2, 2, 2, 0.808);
  font-weight: 700;
}

.post-image {
  width: 100%;
}

.post-image img {
  width: 100%;
  height: 30rem;
}

.actions button {
  background: none;
  border: none;
  padding: 1rem;
}

.actions button svg {
  font-size: 2.5rem;
}

.info-post {
  padding: 0.5rem;
}

.info-post p {
  color: rgba(5, 5, 5, 0.795);
}

.add-comment {
  border-top: 1px solid rgba(2, 2, 2, 0.108);
  position: relative;
  padding-top: 1.5rem;
  margin-top: 1rem;
}

.add-comment textarea {
  width: 100%;
  border: none;
  resize: none;
  outline: none;
  padding-left: 1rem;
}

.add-comment button {
  position: absolute;
  right: 2rem;
  background: none;
  border: none;
  font-size: 1.5rem;
}

.close-post-modal {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 2rem;
  background: none;
  border: none;
}

@media (min-width:660px) {
  .post-details {
    width: 65%;
  }
}

@media (min-width:960px) {
  .post-details {
    width: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: relative;
    height: 50rem;
    position: fixed;
    top: 5%;
    left: 0;
    right: 0;
  }

  .post-details .header {
    width: 50%;
    -ms-flex-order: 1;
        order: 1;
    position: absolute;
    right: 0;
    top: 0;
    border-bottom: 1px solid rgba(2, 2, 2, 0.108);
  }

  .post-image {
    width: 50%;
    -ms-flex-order: 0;
        order: 0;
    position: absolute;
    left: 0;
    top: 0;
  }

  .detail {
    width: 50%;
    -ms-flex-order: 2;
        order: 2;
    position: absolute;
    right: 0;
    bottom: 0;
    border-top: 1px solid rgba(2, 2, 2, 0.108);
  }

  .post-image img {
    height: 50rem;
  }

  .close-post-modal {
    color: #fff;
    font-size: 2.5rem;
  }
}


