.feed {
  width: 100%;
  margin: auto auto;
  padding: 1rem 0;
}

.feed ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.feed ul li {
  width: 100%;
  margin: 0 auto;
  -webkit-animation: fade-in 0.45s linear;
          animation: fade-in 0.45s linear;
}

.header-post {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  padding: 1rem;
  border-radius: 0.5rem 0.5rem 0 0;
}

.header-post img {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
}

.main-post {
  width: 100%;

}

.main-post img {
  width: 100%;
  height: 30rem;
}

.main-post p {
  margin: 0.5rem 0;
  font-size: 1.5rem;
  margin-left: 1rem;
}

.main-post .created-at{
  color: rgba(0, 0, 0, 0.678);
  font-size: 1.25rem;
}

.main-post p span {
  font-weight: 700;
}

.header-post a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  color: black;
  font-size: 1.5rem;
}

.btn-load {
  margin: 1rem auto;
  border: none;
  background: rgb(25, 124, 182);
  color: #fff;
  border-radius: 3px;
  display: block;
  padding: 1rem 2.5rem;
}

.actions button svg.icon {
  font-size: 2rem;
  width: 4rem;
  height: 4rem;
}

@media (min-width: 660px) {
  .feed {
    width: 70%;
  }
}

@media (min-width: 960px) {
  .feed {
    width: 50%;
  }
}

@-webkit-keyframes fade-in {
  0% {
    -webkit-transform: translate3d(0, -15px, 0);
            transform: translate3d(0, -15px, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    -webkit-transform: translate3d(0, -15px, 0);
            transform: translate3d(0, -15px, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}


