.logo {
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 4rem;
    text-align: center;
    color: rgba(7, 7, 7, 0.849);
}

.intro {
    text-align: center;
    font-size: 1.7rem;
    color: rgba(12, 12, 12, 0.4);
    width: 70%;
    margin: 0 auto;
}

.btn-login,
.register-form button {
    display: block;
    margin: 1rem auto;
    border: none;
    border-radius: 4px;
    width: 75%;
    background: rgb(93, 167, 252);
    color: #fff;
    padding: 0.75rem 0.5rem;
    font-size: 1.5rem;
    font-weight: 700;
}

.register-form button:disabled {
    background: rgba(93, 167, 252, 0.671);
    cursor: not-allowed;
}

.register-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.register-form div:not(.spinner, .bounce1, .bounce2, .bounce3) {
    display: block;
    width: 80%;
    text-align: center;
    position: relative;
    height: 4.5rem;
}

.register-form div span {
    position: absolute;
    top: 32.5%;
    right: 1rem;
    color: rgb(245, 50, 50);
    font-size: 1.5rem;
    border: 1.5px solid rgb(245, 50, 50);
    border-radius: 50%;
    padding: 0 0.6rem;
    display: none;
}

.register-form input {
    width: 100%;
    margin: 1rem auto;
    height: 3.5rem;
    border-radius: 4px;
    border: 1px solid rgba(7, 7, 7, 0.14);
    padding: 1rem 2.5rem;
}

hr {
    width: 80%;
    margin: 0 auto;
    border-color: rgba(10, 10, 10, 0.151);
    border-width: 1px;
}

.login p {
    display: block;
    width: 65%;
    margin: 1rem auto;
    text-align: center;
    font-size: 1.5rem;
    color: rgba(5, 5, 5, 0.77);
}

.error-register {
    width: 80%;
    text-align: center;
    color: rgb(247, 71, 71);
    font-size: 1.6rem;
    display: none;
}

.success-register {
    width: 80%;
    text-align: center;
    color: rgb(7, 206, 66);
    font-size: 1.6rem;
    display: none;
}

footer {
    padding: 1rem 0;
}

footer p {
    color: rgb(10, 10, 10);
}

.intro-images {
    background: url('https://www.instagram.com/static/images/homepage/home-phones.png/43cc71bb1b43.png');
    background-position: center center;
    background-repeat: no-repeat;
    height: 600px;
    margin: 0 auto;
    display: none;
    position: relative;
}

.intro-images img {
    position: absolute;
    top: 7.2rem;
    right: 7.5rem;
    width: 25.5rem;
    height: 45rem;
}

@media (min-width: 960px) {
    .login-page {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
    .intro-images {
        width: 50rem;
        display: block;
    }
    .login {
        width: 50%;
    }
}


